.nav {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px;
  display: flex;
  /* justify-content: space-between; */
  /* background-color: #111; */
  height: 30px;
  z-index: 1;
  /* Animation */
  transition-timing-function: ease-in;
  transition: all 0.8s;
}
.nav_black {
  background-color: #111;
}
.nav_logo {
  position: fixed;
  left: 20px;
  width: 80px;
  object-fit: contain;
}
.nav_avatar {
  position: fixed;
  right: 20px;
  width: 30px;
  object-fit: contain;
}
